<template>
	<section v-if="user" id="it-author-profile">
		<vue-headful title="Perfil - Ink & Tales"/>
		<div uk-grid class="uk-grid-collapse">
			<aside class="uk-width-1-3@m uk-width-1-4@xl">
				<div class="uk-section uk-height-viewport">
					<article class="uk-text-center">
						<figure class="it-author-avatar">
							<img src="@/assets/img/placeholder.png" class="uk-border-circle" />
						</figure>
						<h1 class="it-author-name uk-margin-remove">{{ user.username }}</h1>
					</article>

					<dl class="uk-description-list uk-description-list-divider uk-text-left">
						<dt>ID</dt>
						<dd>{{ user.id }}</dd>
						<dt>{{'USERNAME' | translate}}</dt>
						<dd>{{ user.username }}</dd>
						<dt>{{'EMAIL' | translate}}</dt>
						<dd>{{ user.email }}</dd><!-- <dt>Language</dt><dd>{{ selectedLanguage }}</dd> -->
						<dt>Tipo de Plan</dt>
						<dd v-if="user.plan_id == 1">Plan Mensual {{ user.plan_id }}</dd>
						<dd v-if="user.plan_id == 2">Plan Anual {{ user.plan_id }}</dd>
						<dd v-if="user.plan_id != 1 && user.plan_id != 2">Usuario Gratis</dd>
					</dl>
					
				</div>
			</aside>

			<div id="it-author-library" class="uk-width-2-3@m uk-width-3-4@xl">
				<div class="uk-section uk-padding-large">
					<!-- My favorites -->
					<section>
						<h2>{{'MY_FAVORITES' | translate}}</h2>
						<SeriesList
						v-if="user.follow_series.length > 0"
						:series="user.follow_series.map(x => x.series) || []" :classProp="'uk-child-width-1-3@l'"
						></SeriesList>

						<p v-else uk-alert>{{'NO_FAVORITES' | translate}}</p>
					</section>

					<!-- My follow authors -->
					<section class="uk-margin-large-top">
						<h2>Mis autores</h2>
						<AuthorList
						v-if="user.follow_authors.length > 0"
						:authors="user.follow_authors.map(x => x.author) || []"></AuthorList>
						
						<p v-else uk-alert>{{'AUTHOR_NOFOLLOW' | translate}}</p>
					</section>
				</div>
			</div>
		</div>
	</section>
	<Loading v-else />
</template>

<script>
	import SeriesList from "../../components/SeriesList.vue";
	import AuthorList from "../../components/AuthorList.vue";
	import Loading from "../../components/Loading.vue";
	import { USERS_PROFILE_SINGLE_USER } from "../../constants/graphql";
	import { mapGetters } from "vuex";

	export default {
		mounted: function() {
			setTimeout(() => {
				this.id = this.$store.getters["auth/getId"];
				console.log(this.id);
			}, 100);
		},
		data() {
			return {
				api_url: process.env.VUE_APP_STRAPI_API_URL,
				user: null,
				routeParam: this.$route.params.id,
				id: null
			};
		},
		components: {
			SeriesList,
			AuthorList,
			Loading
		},
		computed: {
			...mapGetters("i18n", ["currentLanguage"]),
			selectedLanguage: {
				get() {
					return this.currentLanguage;
				}
			},
		},
		apollo: {
			user: {
				query: USERS_PROFILE_SINGLE_USER,
				fetchPolicy: 'no-cache',
				variables() {
					return { id: this.id };
				},
				skip() {
					return !this.id;
				}
			}
		}
	};
</script>