<template>
	<div v-if="authors" class="uk-grid uk-child-width-1-2 uk-child-width-1-3@m" :class="classProp" uk-grid>
		<div v-for="author in authors" v-bind:key="author.id" class="it-author-card">
			<router-link :to="{ path: '/author/' + author.id }" :key="author.id" class="uk-link-reset">
				<figure class="it-author-avatar">
					<img :src="author.avatar.url" :alt="author.name" v-if="author.avatar" class="uk-border-rounded"/>
					<img src="@/assets/img/placeholder.png" :alt="author.name" class="uk-border-rounded" v-else />
				</figure>
				<h3 class="it-authors-name uk-margin-small-top uk-margin-remove-bottom">{{ author.name }}</h3>
			</router-link>
		</div>
	</div>
	<Loading v-else/>
</template>


<script>
	import { AUTHORS_ALL_SERIES } from "../constants/graphql";
	import Loading from "../components/Loading.vue";
	export default {
		name: "AuthorList",
		components: {
			Loading
		},
		props: {
			classProp: String,
		},
		data: function () {
			return {
				api_url: process.env.VUE_APP_STRAPI_API_URL,
				authors: []
			};
		},
		apollo: {
			authors: AUTHORS_ALL_SERIES
		}
	};
</script>